:root {
  --font-Outfit: "Outfit", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --lightGray: hsl(212, 45%, 89%);
  --grayishBlue: hsl(220, 15%, 55%);
  --darkBlue: hsl(218, 44%, 22%);
  --blueShade: hsl(216, 100%, 61%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-Outfit);
  background-color: var(--lightGray);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-size: 1.375rem;
}

p {
  font-size: 0.9375rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.wrapper {
  margin: 5% auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
@media (max-width: 39.9375em) {
  .wrapper {
    margin: 1.125rem;
  }
}
.wrapper main .container {
  max-width: 20rem;
  max-height: 31.0625rem;
  padding: 1rem;
  background-color: var(--white);
  border-radius: 1.25rem;
}
.wrapper main .container__image {
  max-width: 18rem;
  max-height: 18rem;
  background-color: var(--blueShade);
  border-radius: 0.625rem;
}
.wrapper main .container__image img {
  display: block;
  max-width: 100%;
  height: auto;
}
.wrapper main .container__image .imageClip {
  clip-path: rect(22% 78% 78% 22%);
}
.wrapper main .container__text {
  margin: 1.5rem 0;
  text-align: center;
}
.wrapper main .container__text h1 {
  color: var(--darkBlue);
  padding: 0 1rem 0.5rem 1rem;
  margin: 0;
}
.wrapper main .container__text p {
  margin: 0;
  padding: 0.5rem 1rem 0 1rem;
  color: var(--grayishBlue);
}
.wrapper footer {
  margin-top: 0.625rem;
}
.wrapper footer .attribution {
  font-size: 11px;
  text-align: center;
}
.wrapper footer .attribution a {
  color: #3e52a3;
}